body {
	font-family: 'Roboto', sans-serif;
   line-height: 1.4;
}

.container {
	width: 100%;
	max-width: 1170px;
	margin: 0 auto;
	flex-wrap: wrap;
}

// !===============header===============//
.header {
	display: block;

	&__top {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20px 0;

		.header__top-logo {
			display: flex;
			align-items: center;

			img {
				margin-right: 10px;
			}

			.top__logo-subtext {
				max-width: 290px;
				border-left:1px solid #1676d8;

				p {
					font-size: 12px;
					line-height: 14px;
					margin-left: 10px;
					padding: 8px 0px;
				}
			}
		}

		.header__top-email {
			display: flex;
			flex-direction: column;

			.info {
				display: flex;
				align-items: center;
				margin-bottom: 10px;

				img {
					width: 20px;
					height: 20px;
					margin-right: 5px;
				}

				p {
					font-size: 14px;
					color: #9b373a;
				}

				.telegram:hover {
					font-size: 16px;
					color: #9b373a;
					cursor: pointer;
				}

				.email:hover {
					color: #9b373a;
					cursor: pointer;
				}
			}
		}

		.header__top-btn {
			display: flex;
			flex-direction: column;
			text-align: center;
			align-items: center;

			.top__btn-b {
				background: #1676d8;
				color: #fff;
				text-decoration: none;
				font-size: 14px;
				text-align: center;
				width: 262px;
				padding: 10px 0;
				margin-bottom: 10px;
			}

			.top__btn-g {
				background: #73bc13;
				color: #fff;
				text-decoration: none;
				font-size: 14px;
				text-align: center;
				width: 262px;
				padding: 10px 0;
				margin-bottom: 20px;
			}

			.btn:hover {
				cursor: pointer;
				opacity: 0.9;
			}
		}
	}

	&__nav {
		border-bottom: 2px solid #1676d8;
		background: #f5f5f5;
		 
		ul {
			display: flex;
			justify-content: space-between;

			li {
				text-transform: uppercase;
				font-size: 14px;
				padding: 11px 15px;
			}

			li:hover {
				cursor: pointer;
				color: #fff;
				text-decoration: underline;
				background: #1676d8;
			}
		}
	}
}

// !===============main===============//
.main {
	display: flex;
	justify-content: center;
	background: url(/src/img/img1.jpg)50% 50% no-repeat;
	background-size: cover;
	min-height: 320px;

	&__title-text {
		max-width: 620px;
		display: flex;
		text-align: center;
		align-items: center;

		h1 {
			text-transform: uppercase;
			font-size: 26px;
			font-weight: 300;
			line-height: 32px;
			color: #fff;
		}
	}
}

// !===============info===============//
.info {
	display: flex;

	&__card {
		background: #deca39;
		width: 33.333%;

		.info__card-img {
			img {
				width: 86px;
				height: 86px;
				position: relative;
				top: -25px;
				left: 40%
			}
		}

		.info__card-text {
			text-align: center;

			h2 {
				font-weight: 700;
				text-transform: uppercase;
				color: #fff;
				margin-bottom: 5px; 
			}

			p {
				font-size: 14px;
				line-height: 18px;
				color: #fff;
				padding-bottom: 50px; 
			}
		}
	}

	.orange {
		background: #f34529;
	}

	.green {
		background: #ef8c11;
	}

	&__card:hover {
		opacity: 0.9;
		cursor: pointer;
	}
}

// !===============forms===============//
.forms {
	display: flex;
	justify-content: space-between;

	&__aboutus {
		display: block;

		h3 {
			font-size: 24px;
			font-weight: 400;
			line-height: 30px;
			margin-top: 35px;
			margin-bottom: 35px;

			border-bottom: 3px solid #1676d8;
		}
		
		.form {
			display: flex;
			flex-direction: column;
			margin: 50px 0 0 80px;

			input {
				border: none;
				background: #f5f5f5;
				padding: 12px 24px;
				font-size: 18px;
				width: 380px;
				height: 27px;

				margin-bottom: 20px;
			}

			input:focus {
				box-shadow: 0px 0px 10px 3px rgb(78, 164, 214);;
				outline: 0;
				outline-offset: 0;
				
			}

			button {
				color: #fff;
				background: #1676d8;
				border: none;
				font-size: 18px;
				padding: 12px 24px;
				width: 428px;
				height: 45px;
			}

			button:hover {
				cursor: pointer;
				opacity: 0.8;
				transition: .5s;
			}
		}
	}
	
	&__img {
		margin: 120px 0 100px 0;

		img {
			display: block;
			width: 560px;
			height: 345px;
		}
	}
}

// !===============tabs===============//
.tabs {
	&__tab {
		background: url(/src/img/tabs-img.png)50% 50% no-repeat;
		background-size: cover;
		display: flex;
		justify-content: space-between;

		.tabs__tab-items {
			display: flex;
			flex-direction: column;
			padding: 60px 0 70px 350px;

			.tabs__tab-child {
				color: #fff;
				text-transform: uppercase;
				text-decoration: none;
				font-size: 14px;
				padding: 10px 230px 10px 20px;
			}

			.tabs__tab-child:hover {
				color: #1676d8;
				background: #fff;
				font-weight: bold;
			}
		}

		.tabs__body {
			display: block;

			.tabs__block {
				padding:  60px 370px 0px 0px;
				color: #fff;
				display: none;
				width: 515px;
				
				h3 {
					font-size: 16px;
					margin-bottom: 15px;
				}

				p {
					font-size: 14px;
					margin-bottom: 15px;
					line-height: 16px;
				}
			}

			.tabs__block:target {
				display: block;
			}
		}
	}
}

// !===============services===============//
.services {
	&__items {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		grid-template-rows: 1fr 1fr;
		row-gap: 70px;
		margin: 70px 0;

		.services__items-item {
			display: flex;
			align-items: center;
			max-width: 295px;

			img {
				width: 93px;
				height: 93px;
				margin-right: 20px;
			}

			p {
				font-size: 12px;
				line-height: 1.2;
				color: #000;
			}
		}
	}
}

// !===============subinfo===============//
.subinfo {
	background: #1676d8;

	&__title {
		background: #f5f5f5;
		display: flex;
		padding: 40px 0px 20px 0;

		h4 {
			font-size: 36px;
			font-style: normal;
			color: #ed6a24;
		}

		h4::after {
			display: block;
			width: 80px;
			height: 3px;
			margin-top: 10px;
			content: '';
			background: #ed6a24;
		}

		.tre {
			margin-left: 450px;
		}

		.bro {
			margin-left: 390px;
		}
	}

	&__text {
		display: flex;
		justify-content: space-around;
		padding: 25px 0 60px 0;
		
		.subinfo__text-content {
			.content-item {
				display: flex;
				width: 416px;
				margin-bottom: 20px;

				img {
					display: block;
					width: 12px;
					height: 18px;
					margin-right: 10px;
				}

				p {
					color: #fff;
					font-size: 14px;
					line-height: 1.3;
				}
			}
		}
	}
}

